.todo-list-item {
    font-size: 1.25rem;
}

.todo-list-item button {
    width: 50px;
    margin: 5px;
}

.todo-list-item-label {
    margin-left: 1rem;
    line-height: 40px;
    cursor: pointer;
    user-select: none;
}

.todo-list-item.done .todo-list-item-label {
    text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
    font-weight: bold;
    color: steelblue;
}
